import React from 'react';
import config from '../../../../config/config';
import { format, parseISO, toDate } from 'date-fns';
import { useSelector } from 'react-redux';
import { Box, Paper, Typography, Divider } from '@mui/material';
import MemberProfileTabs from '../../tabs/MemberProfileTabs';
// react-icons  
import {
    FaAt,
    FaBirthdayCake,
    FaCalendarDay,
    FaInfoCircle,
    FaMapMarkerAlt,
    FaPhoneAlt,
    FaUser
} from 'react-icons/fa';

export default function MyProfileDetailsPage() {

    const authRedux = useSelector((state) => state.auth.value);

    let member = authRedux?.me ?? null;
    let address, isBirthday, birthdate, middle_name = null;
    let permanentAddress, presentAddress = "Not set";

    if (member) {
        address = member?.address;
        isBirthday = format(parseISO(member?.birthdate ?? Date.now()), "MMMM dd") === format(toDate(Date.now()), "MMMM dd");
        birthdate = format(parseISO(member?.birthdate ?? Date.now()), "MMMM dd, yyyy");
        middle_name = member?.middle_name.length > 0 ? `${member?.middle_name[0]}.` : '';
        
        let presentAdd, permAdd = null;
        if (address.length > 0) {
            if (address[0]?.address_type === "present") {
                presentAdd = address[0];
                permAdd = address[1];
            } else {
                if (address.length > 1) {
                    permAdd = address[0];
                    presentAdd = address[1];
                }
            }
        }


        if (presentAdd?.zipcode.zipcode !== null) {
            presentAddress = `${presentAdd?.streetpurok ?? 'Not Set'}, ${presentAdd?.barangay ?? ''}, ${presentAdd?.zipcode.zipcode ?? ''}`;
        }

        if (permAdd?.zipcode.zipcode !== null) {
            permanentAddress = `${permAdd?.streetpurok ?? 'Not Set'} ${permAdd?.barangay ?? ''} ${permAdd?.zipcode.zipcode ?? ''}`;
        }

    }

    return (
        <Box display="flex" flexDirection="column" sx={{ width: '100%', p: 1 }}>
            <Box display="flex" flexDirection="row" sx={{ backgroundColor: '#eee', p: 1, minHeight: 55 }}>
                <Paper elevaiont={2} square sx={{ p: 1, borderRadius: 0, width: '100%' }} >
                    <Box sx={{ m: 0 }}>
                        <Box
                            display="flex"
                            flexGrow={1}
                            flexDirection="column"
                            sx={{
                                mb: 1,
                                width: '100%',
                                backgroundColor: "#00BFA5",
                                color: "#FFF"
                            }}>
                            <Typography variant="h6" sx={{ p: 1, pl: 2 }}>
                                <FaInfoCircle size={18} style={{ marginRight: 5, color: '#fff' }} />
                                Quick Overview
                            </Typography>
                            <Box display="flex" flexDirection="row" justifyContent="flex-start" sx={{ width: "100%", mt: -2 }}>
                                <Box sx={{ width: "31.5%", m: 1 }}>
                                    <Paper elevation={2} sx={{ p: 1, pl: 2, m: 1, width: '100%', display: "flex", flexDirection: "row" }}>
                                        <Box sx={{ width: '75%', borderRight: '1px solid #eee' }}>
                                            <Typography variant="subtitle1">Full Name</Typography>
                                            <Typography variant="subtitle1" fontWeight="bolder" noWrap>{`${member?.first_name} ${middle_name} ${member?.last_name}`}</Typography>
                                            {isBirthday &&
                                                <Typography variant="caption" color="secondary" noWrap>
                                                    <FaBirthdayCake /> Today is {member?.gender.toLowerCase() === "male" || member?.gender.toLowerCase() === "m" ? 'his' : 'her'} birthday
                                                </Typography>
                                            }
                                            {!isBirthday &&
                                                <Typography variant="caption">
                                                    <FaCalendarDay /> Date of Birth: {birthdate ?? '...'}
                                                </Typography>
                                            }
                                        </Box>
                                        <Box sx={{ width: '25%' }} display="flex" flexDirection="row" justifyContent="center" alignItems="center">
                                            <FaUser size={50} style={{ color: "#00BFA5", opacity: 0.7 }} />
                                        </Box>
                                    </Paper>
                                </Box>
                                <Box sx={{ width: "31.5%", m: 1 }}>
                                    <Paper elevation={2} sx={{ p: 1, pl: 2, m: 1, width: '100%', display: "flex", flexDirection: "row" }}>
                                        <Box sx={{ width: '75%', borderRight: '1px solid #eee' }}>
                                            <Typography variant="subtitle1">Contact Info</Typography>
                                            <Typography variant="subtitle1" fontWeight="bolder" noWrap>{'+639' + member?.contact_no ?? '###-####-###'}</Typography>
                                            <Typography variant="caption" noWrap>
                                                <FaAt /> Email: {member?.user.email ?? '...'}
                                            </Typography>
                                        </Box>
                                        <Box sx={{ width: '25%' }} display="flex" flexDirection="row" justifyContent="center" alignItems="center">
                                            <FaPhoneAlt size={50} style={{ color: "#1976D2", opacity: 0.7 }} />
                                        </Box>
                                    </Paper>
                                </Box>
                                <Box sx={{ width: "31.5%", m: 1 }}>
                                    <Paper elevation={2} sx={{ p: 1, pl: 2, m: 1, width: '100%', display: "flex", flexDirection: "row" }}>
                                        <Box sx={{ width: '75%', borderRight: '1px solid #eee' }}>
                                            <Typography variant="subtitle1">Present Address</Typography>
                                            <Typography variant="subtitle1" fontWeight="bolder" noWrap> {presentAddress ?? '...'}</Typography>
                                            <Typography variant="caption" noWrap>
                                                <FaMapMarkerAlt /> Permanent: {permanentAddress ?? '...'}
                                            </Typography>
                                        </Box>
                                        <Box sx={{ width: '25%' }} display="flex" flexDirection="row" justifyContent="center" alignItems="center">
                                            <FaMapMarkerAlt size={50} style={{ opacity: 0.7, color: '#E45570' }} />
                                        </Box>
                                    </Paper>
                                </Box>
                            </Box>
                        </Box>
                    </Box>
                </Paper>
            </Box>
            <Divider />
            <Box display="flex" flexDirection="row" sx={{ width: '100%', backgroundColor: '#fff', mt: 1 }}>
                <Box display="flex" flexDirection="column" flexGrow={1} sx={{ minHeight: '100vh' }}>
                    <Paper sx={{ p: 2, m: 0, borderRadius: 0, width: '100%' }}>
                        <MemberProfileTabs />
                    </Paper>
                </Box>
            </Box>
        </Box>
    )
}
