import React from 'react';
import Grid from '@mui/material/Grid';
// react-icons
import CredentialTabs from '../../tabs/CredentialTabs';
import MyCredentialDetailsPage from './MyCredentialDetailsPage';

export default function MyCredentialsPage(props) {
    
    return (
        <div style={{ height: '100%', width: '100%' }}>
            <Grid container spacing={1}>
                <Grid item xs={12}>
                    <MyCredentialDetailsPage />
                </Grid>
                <Grid item xs={12}>
                    <CredentialTabs />
                </Grid>
            </Grid>
        </div>
    )
}
